export default{
  props: {
    name: {
      type: String,
      default: 'photo',
    },
    dest: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      default: 'Crop Photo',
    },
    subtitle: {
      type: String,
      default: ''
    },
    crop: {
      type: Boolean,
      default: true,
    },
    options: {
      type: Object,
      default () {
        return {
          fixed_size: true,
          mode: 'square',
        };
      },
    },
  },
  data() {
    return {
      cropping: false,
      uploading: false,
      progress: 0,
      error: null,
      context: null,
    };
  },
  computed: {
    empty () {
      const cropping = this.cropping;
      const uploading = this.uploading;

      return !(cropping || uploading);
    },
  },
  methods: {
    cropAndUpload () {
      if (!this.context) return;
    },
    upload (payload) {
      const form = new FormData();

      form.append(this.name, payload);

      this.uploading = true;
      this.progress = 0;
      this.error = null;

      this.$http.post(this.dest, form, {
          progress: (event) => {
            if (event.lengthComputable) {
              this.progress = event.loaded / event.total * 100;
            }
          },
        })
        .then((response) => {
          this.uploading = false;

          const src =  response.headers.get('Location');
          this.$emit('uploaded', src, response);
        })
        .catch((response) => {
          this.uploading = false;

          if ('json' in response) {
            response.json().then((result) => {
              this.error = result.message || 'Upload failed.';
            }).catch((error) => {
              this.error = error;
            });
          } else {
            this.error = response;
          }
        });
    },
    onFileSelected (event) {
      const files = event.target.files;

      if (!files || !files[0]) return;

      return this.upload(files[0]);

      // const ext = file.substring(file.lastIndexOf('.') + 1).toLowerCase();

      // if (['png', 'gif', 'jpg', 'jpeg'].indexOf(ext) < 0) return;

      // const FD = new FileReader();

      // FD.onload((event) => {
        // this.context = new Crop('#profile-photo-uploader-cropper', event.target.result, this.options);
      // });

      // FD.readAsDataURL(selected[0]);
    },
  },
};



